       "name":"5. 需卦",
       "title":"需，有孚，光亨，貞吉，利涉大川。",
       "head":"諸事：龍困淺灘，時機未到，靜候時機為宜。\n愛情：雙方有阻隔，但強行是無法得到的。\n事業：雖有能力，但目前還沒有適當的機會。先設法渡過眼前危機為宜。\n建議：需卦是藏有危機的一卦，雖然你的能力足以化險為夷，但仍不宜妄動，靜待佳機才是比較好的策略。\n",
       "content":"需為「等待」的意思。需卦則是龍困淺灘的時候，宜於等待時機。二月之卦。\n上為水險，下為剛健。人雖剛健而有能力，但眼前卻有水險阻隔，而無法前進，為龍困淺灘，等待時機之象。\n卦象為水上於天成雲，雲在天上不能成雨，雨象徵的是陰陽調和與開花結果。雲在天上，則必需等待更多水氣之累積，與天時地利才能夠下雨。比喻條件未齊備，必需再等待時機成熟才能夠發揮你的實力。\n雖有剛健的德性而可以涉險，能力足以化險為夷。但眼下仍以退守、靜待時機之成熟才是上策。所以《雜卦傳》說：｢需，不進也。｣意謂處｢需｣的時候，不當前進。\n在卦序上需卦是繼屯蒙而來，屯、蒙為文明初開，人民草昧無知的時候，需卦講的則是開始教民農漁，解決飲食基本問題之事，所以《序卦》說：｢蒙者物之稚也。 物稚不可不養也，故受之以需，需者飲食之道也。｣《象》傳說：｢雲上於天，需，君子以飲食宴樂。｣君子之所以｢飲食宴樂｣，是因為危險在前，時機未到，所以養精畜銳，等待時機。\n",
       "imgPath":"image/5.png"
