       "name":"6. 訟卦",
       "title":"訟，有孚，窒惕，中吉，終凶，利見大人，不利涉大川。",
       "head":"諸事：中庸、退讓，放棄堅持則吉。\n愛情：爭吵、不和。\n事業：有爭議，有法律的風險。\n建議：注意福禍相倚，得未必是福，失未必是禍。你的堅持是很危險的，凡事不要走極端，不要頑強固執。能夠放棄爭訟則吉，平安是福；若是一意爭取到底，既使贏了也會召來後續的禍害。\n",
       "content":"官司、爭吵、爭議。先吉後凶。凡事放棄內心的堅持為上策。\n訟卦內水險而外剛健，象徵一個人的想法相當危險，而又倔強求勝；對於自己的危險想法堅持到底，一意孤行。因此而與人興訟、爭吵。\n訟卦乾剛在上，水險在下，又有上司（乾）以氣勢威迫下屬，而下屬以危險（坎）的行為因應的卦象。又乾天之陽氣停留在上（有否卦之象），水本就潤下，但又停留於下，所以天與水兩者背道而馳，沒有交集。\n訟卦是禍中有福，福中有禍，福禍得失無常的一卦。往往先吉者後來變為凶，反之則先凶則後吉。\n放下堅持、退一步海闊天空，絕對是卜到訟卦的最佳對策。\n",
       "imgPath":"image/6.png"
