       "name":"7. 師卦",
       "title":"師貞，丈人吉，旡咎。",
       "head":"諸事：責任重大，憂慮很深。對事情經驗豐富者為吉。沒經驗者為凶。\n愛情：異性關係複雜，難以處理而爭吵。\n事業：責任重大，很具挑戰性。無經驗勿試。\n建議：帶兵打仗首先要師出有名，其次用人則是要找老成持重，有豐富疆場經驗者為宜。無經驗者絕不可用。\n",
       "content":"以寡統眾，帶兵打仗。責任重大，憂慮很深。\n師是一人統領眾人，帶兵打仗的意思。卜到此卦表示事情複雜，責任重大，因此讓人憂心重重。\n上面為地，下面有水，處於危險而能以柔順的態度來應付。柔順之君王授命於剛健的將帥，為授命出征之象。\n舉凡卜到師卦，事情多數多憂。若是與人事相關的事情，則以選擇年老持重而有處事經驗的長者最好，比較有成功的可能。反之，若選擇年輕而無經驗者，則一敗塗地的機會很大。\n在卦序上，師卦與比卦為上下相反的一對綜卦，是繼需、訟兩卦而來。《序卦》：「訟必有眾起，故受之以師，師者眾也。」蓋師卦是因為兩國爭訟，因此聚眾出師以解決爭端，也是戰爭的意思。\n",
       "imgPath":"image/7.png"
